body {
	background: linear-gradient( #000, #013 );
	color: #fff;
	height: 100%;
	min-height: 100vh;
	margin: 0;
	padding: 0;
	font-family: 'Quicksand', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{
	margin: 0 auto 30px auto;
	padding: 30px 0 0 0;
	text-align: center;
	width: 80%;
}

h1{
	color: #fff;
	font-size: 60px;
	font-weight: 300;
	margin: 0;
	text-transform: uppercase;
}
h3{
	margin: 25px 0 10px 0;
}

.app-main{
	text-align: center;
}

.counter{
	display: block;
	margin-bottom: 20px;
}

button{
	background: radial-gradient( #9f5, #4c7 );
	border: 0;
	border-radius: 5px;
	box-shadow: 0 0 10px rgba(200,255,150,0.7);
	color: #fff;
	cursor: pointer;
	display: inline-block;
	font-family: 'Quicksand', sans-serif;
	font-size: 20px;
	font-weight: 700;
	letter-spacing: 2px;
	line-height: 1;
	margin: 0 10px 0 0;
	padding: 10px 20px;
	text-transform: uppercase;
	vertical-align: top;
}

.clear{
	background: radial-gradient( #eee, #aaa );
	box-shadow: 0 0 10px rgba(255,255,255,0.3);
	color: #520;
}

.count{
	background: radial-gradient( #3af, #258 );
	border-radius: 5px;
	box-shadow: 0 0 10px rgba(50,200,250,0.7);
	color: #cef;
	display: inline-block;
	font-size: 20px;
	font-weight: 700;
	margin-right: 10px;
	letter-spacing: 1px;
	line-height: 1;
	padding: 10px 20px;
	text-transform: uppercase;
	vertical-align: top;
}
.count.warn{
	background: radial-gradient( #ee4, #ca0 );
	box-shadow: 0 0 10px rgba(255,200,10,0.9);
	color: #c30;
}

.increment.up{
	background: radial-gradient( #9f5, #2a5 );
	box-shadow: 0 0 10px rgba(200,255,150,0.7);
	color: #373;
}
.increment.down{
	background: radial-gradient( #f30, #a10 );
	box-shadow: 0 0 10px rgba(255,100,20,0.7);
	color: #520;
}

.delete{
	background: radial-gradient( #c00, #500 );
	border-radius: 5px;
	box-shadow: 0 0 10px rgba(255,50,50,0.5);
	color: #fec;
	display: inline-block;
	font-size: 20px;
	font-weight: 700;
	margin-right: 10px;
	letter-spacing: 1px;
	line-height: 1;
	padding: 10px 20px;
	text-transform: uppercase;
	vertical-align: top;
}
